.Homepage {
    height: 800px;
}

.HomepageColored {
    background-color: rgb(120,41,43);
    height: inherit;
    clip-path: ellipse(80% 60% at 0 0);
}

h1 {
    font-family: 'Playfair Display', serif;
}

.HomeText {
    width: 100%;
    position: absolute;
    top: 80px;
    height: 700px;
}

.FirstLine {
    position: absolute;
    left: 10%;
    top: 2%;
    font-size: 4.4rem;
    color: white
}

.LogoBox {
    position: absolute;
    right: 40%;
    left: 40%;
    top: 26%;
    height: auto;
    border-radius: 100%;
    background-color: rgb(16,16,16);
}

.ThirdLine {
    position: absolute;
    top: 60%;
    right: 10%;

    font-size: 5.2rem;
    color: rgb(120,41,43);
}

.LogoBox img {
    width: 100%;
    height: 100%;
}

