.Contacts {
    padding-bottom: 150px;
}

.Contacts input {
    display: block;
    margin-left: 225px;
    margin-top: 25px;
    border: none;
    border-bottom: 2px solid rgb(120,41,43);
    width: 250px;
    height: 35px;
    font-size: 1.2rem;
    color: black;
    background-color: rgb(16,16,16);
    color: rgb(255, 249, 241);
}

.Contacts input::placeholder {
    font-size: 1.2rem;
}

.Contacts .Inputs {
    margin-bottom: 100px;
}
.Form textarea {
    width: 500px;
    height: 350px;
    font-size: 1.2rem;
    padding: 15px;
    resize: none;
    border: 10px solid rgb(120,41,43);
    border-top: none;
    border-bottom: none;
    clip-path: polygon(0 0, 10px 10px, 540px 10px, 550px 0, 550px 380px, 540px 370px, 10px 370px, 0 380px, 0 0);
    background-color: rgb(16,16,16);
    color: rgb(255, 249, 241);
    user-select: none;
} 


/* Remember in the media queries above to add padding and border */

.Form p {
    margin-top: 0;
    margin-bottom: 40px;
}


.Form button {
    display: block;
    margin: auto;
    width: 100px;
    height: 45px;
    border-radius: 10px;
    border: 2px solid #297876;
    background-color: rgb(120,41,43);
    color: #297876;
    font-weight: bold;
    font-size: 0.85rem;
}

@media (max-width: 700px) {
    .Form textarea {
        width: 300px;
    }

    .Contacts input {
        margin-left: auto;
        margin-right: auto;
    }

}

@media (max-width: 350px) {
    .Form textarea {
        width: 250px;
    }
}