.Container {
    display: inline-block;
    cursor: pointer;
    position: fixed;
    top: 30px;
    right: 45px;
    z-index: 20;
    width: 50px;
    height: 50px;
}
  
.Container div {
    width: 35px;
    height: 5px;
    margin: 6px 0;
    transition: 0.4s;
    border-radius: 10px;
}

.ChangeBright {
    background-color: rgb(16,16,16);
}

.ChangeDark {
    background-color: rgb(120,41,43);
}

.change1{
    transform: rotate(-45deg) translate(-9px, 6px);
}
.change2 {opacity: 0;}
  
.change3 {
    transform: rotate(45deg) translate(-8px, -8px);
}


@media (max-width: 500px) {
    .ChangeBright {
        background-color: rgb(120,41,43);
    }
    
    .ChangeDark {
        background-color: rgb(120,41,43);
    }

    .Container {
        right: 20px;
    }
}
